/* ===================================== 
   Base Element Styles
======================================== */
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

body {
  /* font-size: 1.4em; */
  font-family: 'Varela Round', sans-serif;
  /* font-weight: bold; */
  color: #FFFFFF;
  padding: 0;
  margin: 0;
  height: 100%;
  background-color: body light-grey;
  background-image: url("/assets/wallpapers/18.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  overflow: hidden;
  /* background-size: cover; */ }

/* ===================================== 
   Main Layout Styles
======================================== */
.main {
  text-align: center; }

#App {
  width: 100%; }

/* controls pop-out tab */
#main {
  margin-right: 0px; }

/* ===================================== 
   Main Top Layout
======================================== */
.main-top {
  margin-bottom: 8vh;
  /*this will cause overflow*/
  /* display: flex; */
  /* height: 33vh; */
  /* justify-content: center; */ }

/* ---- Time ---- */
.time {
  text-transform: uppercase;
  font-size: 600%;
  font-weight: bold;
  text-shadow: 0 2px 6px rgba(57, 57, 57, 0.26);
  margin-top: 15vh;
  margin-bottom: 5vh; }

/* ---- Search Area ---- */
.search-area {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column; }

.search-type {
  font-size: .8rem;
  display: -ms-flexbox;
  display: flex;
  width: 38vw;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 1vh;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5);
  letter-spacing: 2px;
  padding-left: 5%;
  padding-right: 15%; }

.search-input-box {
  min-width: 300px;
  background: #FFFFFF;
  color: #7E7E7E;
  width: 40vw;
  height: 4vh;
  display: -ms-flexbox;
  display: flex;
  border-radius: 20px;
  -webkit-box-shadow: none;
          box-shadow: none;
  border: none;
  margin-bottom: 60px;
  /* box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5); */ }

/* The text "Google" inside the search bar */
.box-item {
  -ms-flex-pack: start;
      justify-content: flex-start;
  padding-left: 20px;
  -ms-flex-item-align: center;
      align-self: center;
  font-size: .9rem;
  letter-spacing: 2px; }

/* ===================================== 
   Main Grid Layout
======================================== */
.main-grid {
  max-width: 60vw;
  /* height: 66vh; */
  display: grid;
  /* grid-template-columns: 1fr 1fr 1fr; */
  /* grid-template-rows: 100px 100px; */
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-column-gap: 4rem;
  grid-row-gap: 0.5rem;
  margin: auto;
  /* grid-auto-columns: 1fr; */
  /* grid-auto-flow: column; */ }

.main-grid p {
  font-size: .8rem;
  letter-spacing: 2px;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.5); }

